import React from "react";
import "./App.css";
import ApolloCLient from "apollo-boost";
import { ApolloProvider } from "react-apollo";
import Launches from "./components/launch/launches";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import Launch from "./components/launch/launch";

import SpaceXLogo from "./assets/img/spacex-logo.jpg";
import Home from "./components/home";
import Capsules from "./components/capsule/capsules";
import Cores from "./components/core/cores";
import Core from "./components/core/core";
import Crews from "./components/crew/crews";

const client = new ApolloCLient({
  uri: "/graphql",
});

function App() {
  return (
    <ApolloProvider client={client}>
      <Router>
        <div className="container-fluid p-3 bg-light">
          <div
            style={{
              display: "flex",
              justifyContent: "center",
              width: "30px",
              height: "auto",
              margin: "0 auto",
            }}
          >
            <img src={SpaceXLogo} alt="Space X Logo" />
          </div>
        </div>
        <Switch>
          <Route exact path="/" component={Home} />
          <Route exact path="/launches" component={Launches} />
          <Route exact path="/launch/:flight_number" component={Launch} />
          <Route path="/capsules" component={Capsules} />
          <Route exact path="/cores" component={Cores} />
          <Route exact path="/cores/:id" component={Core} />
          <Route exact path="/crew" component={Crews} />
        </Switch>
      </Router>
    </ApolloProvider>
  );
}

export default App;
