import React from "react";
import classNames from "classnames";
import { Link } from "react-router-dom";

function CoreItem({
  core: {
    last_update,
    serial,
    status,
    id,
  },
}) {
  return (
    <div className="card card-body mb-3 text-dark">
      <div className="row">
        <div className="col-md-9">
          <h4>
            {" "}
            Status :{" "}
            <span
              className={classNames({
                "text-danger": status === "lost",
                "text-info": status === "inactive",
                "text-success": status === "expended",
              })}
            >
              {status}
            </span>
          </h4>
          <p>Last Update: {last_update}</p>
          <p>Serial : {serial}</p>
          <p>ID: {id}</p>
        </div>
        <Link to={`/cores/${id}`}>Tap to View More</Link>
      </div>
    </div>
  );
}

export default CoreItem;
