import React from "react";
import classNames from "classnames";

function CapsuleItem({
  capsule: {
    reuse_count,
    water_landings,
    land_landings,
    last_update,
    serial,
    status,
    id,
  },
}) {
  return (
    <div className="card card-body mb-3 text-dark">
      <div className="row">
        <div className="col-md-9">
          <h4>
            {" "}
            Status : {" "}
            <span
              className={classNames({
                "text-danger": status === "retired",
                "text-info": status === "unknown",
                "text-success": status === "active",
              })}
            >
              {status}
            </span>
          </h4>
          <p>Reuse Count : {reuse_count}</p>
          <p>Water Landings : {water_landings}</p>
          <p>Land Landings : {land_landings}</p>
          <p>Last Update: {last_update}</p>
          <p>Serial : {serial}</p>
          <p>ID: {id}</p>
        </div>
      </div>
    </div>
  );
}

export default CapsuleItem;
