import React, { Fragment } from "react";
import gql from "graphql-tag";
import { Query } from "react-apollo";
import { Link } from "react-router-dom";
import classNames from "classnames";

const CORE_QUERY = gql`
  query CoreQuery($id: String!) {
    core(id: $id) {
      block
      reuse_count
      rtls_attempts
      rtls_landings
      asds_attempts
      asds_landings
      last_update
      serial
      status
      id
    }
  }
`;

export default function Core(props) {
  let { id } = props.match.params;
  id = id.toString();

  return (
    <Fragment>
      <Query query={CORE_QUERY} variables={{ id }}>
        {({ loading, error, data }) => {
          if (loading) return <h4>Loading...</h4>;
          if (error) console.log(error);
          const {
            block,
            reuse_count,
            rtls_attempts,
            rtls_landings,
            asds_attempts,
            asds_landings,
            last_update,
            serial,
            status,
            id,
          } = data.core;
          return (
            <div className="container-fluid ml-5">
              <h1>
                {" "}
                Status :{" "}
                <span
                  className={classNames({
                    "text-danger": status === "lost",
                    "text-info": status === "inactive",
                    "text-success": status === "expended",
                  })}
                >
                  {status}
                </span>
              </h1>
              <h4 className="mb-3">Core details</h4>
              <ul className="list-group">
                <li className="list-group-item">Block : {block}</li>
                <li className="list-group-item">Reuse Count : {reuse_count}</li>
                <li className="list-group-item">Last Update : {last_update}</li>
                <li className="list-group-item">Reuse Count : {reuse_count}</li>
                <li className="list-group-item">Serial : {serial}</li>
                <li className="list-group-item">ID : {id}</li>
              </ul>
              <h4 className="my-3">Return To Launch Site Details</h4>
              <ul className="list-group">
                <li className="list-group-item">
                  RTLS Attemps : {rtls_attempts}
                </li>
                <li className="list-group-item">
                  RTLS Landings : {rtls_landings}
                </li>
                <li className="list-group-item">Serial : {serial}</li>
              </ul>
              <h4 className="my-3">Autonomous Spaceport Drone Ship Details</h4>
              <ul className="list-group">
                <li className="list-group-item">
                  ASDS Attemps : {asds_attempts}
                </li>
                <li className="list-group-item">
                  ASDS Landings : {asds_landings}
                </li>
                <li className="list-group-item">Serial : {serial}</li>
              </ul>
              <hr />
              <Link to="/cores" className="btn btn-secondary">
                Back
              </Link>
            </div>
          );
        }}
      </Query>
    </Fragment>
  );
}
