import React, { Fragment } from "react";
import { gql } from "apollo-boost";
import { Query } from "react-apollo";
import CapsuleItem from "./capsuleitem";

const LAUNCHES_QUERY = gql`
  query LaunchesQuery {
    capsules {
      reuse_count
      water_landings
      land_landings
      last_update
      serial
      status
      id
    }
  }
`;

export default function Capsules() {
  return (
    <div className="container container-fluid bg-dark text-white">
      <h1 className="display-4 m-3 p-5">Capsules</h1>
      <Query query={LAUNCHES_QUERY}>
        {({ loading, error, data }) => {
          if (loading) return <h4>Loading...</h4>;
          if (error) console.log(error);

          return (
            <Fragment>
              {data.capsules.map((capsule) => (
                <CapsuleItem key={capsule.id} capsule={capsule} />
              ))}
            </Fragment>
          );
        }}
      </Query>
    </div>
  );
}
