import React, { Fragment } from "react";
import gql from "graphql-tag";
import { Query } from "react-apollo";
import CoreItem from "./coreitem";

const CORE_QUERY = gql`
  query CoreQuery {
    cores {
      block
      reuse_count
      rtls_attempts
      rtls_landings
      asds_attempts
      asds_landings
      last_update
      serial
      status
      id
    }
  }
`;

function Cores() {
  return (
    <div className="container container-fluid bg-dark text-white">
      <h1 className="display-4 m-3 p-5">Cores</h1>
      <Query query={CORE_QUERY}>
        {({ loading, error, data }) => {
          if (loading) return <h4>Loading...</h4>;
          if (error) console.log(error);

          return (
            <Fragment>
              {data.cores.map((core) => {
                return <CoreItem key={core.id} core={core} />
              })}
            </Fragment>
          );
        }}
      </Query>
    </div>
  );
}

export default Cores;
